@import url(../fonts/stylesheet.css);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Gilroy';
    outline: 0;
}

$bigBold: 4.2vw;
$bigBoldLh: 4vw;

$mediumRegular: 1.4vw;
$mediumRegularLh: 2vw;

$smallRegular: 1.1vw;
$smallRegularLh: 1.2vw;

.big-bold {
    font-size: $bigBold;
    line-height: $bigBoldLh;
    font-weight: 700;
}
.medium-regular {
    font-size: $mediumRegular;
    line-height: $mediumRegularLh;
    font-weight: 400;
}
.small-regular {
    font-size: $smallRegular;
    line-height: $smallRegularLh;
    font-weight: 400;
}

.container {
    width: 92.5%;
    height: 100%;
    margin: 0 auto;
}
.hidden-element {
    display: none;
}

.App {
    overflow: hidden;
    .page-projects {
        height: 72vw;
        background-color: #F7F4EB;
        padding-top: 5.6vw;
        .ph-top {
            position: absolute;
            top: 0;
            left: 0;
            transform: translateZ(0.0099vw);
        }
        .ph-back {
            transform:scale(-1, 1);
        }
        .flip-group {
            width: 100%;
            height: auto;
            position: relative;
            transition: all 0.4s ease-in;
            transform-style: preserve-3d;
        }
        .two-pc:hover {
            .flip-group {
                transform: rotate3d(0, 1, 0, -180deg);
            }
        }
        .projects-group-one {
            display: flex;
            margin-top: 5.6vw;
            margin-bottom: 5.6vw;
            .projects-group-one__photo {
                width: 25vw;
                height: 18vw;
                margin-right: 2.8vw;
                img {
                    width: 100%;
                    height: auto;
                }

            }
        }
        .projects-group-two {
            display: flex;
            justify-content: space-between;
            margin-left: 4vw;
            .projects-group-two__photo {
                width: 32vw;
                height: 24vw;
                margin-right: 2.8vw;
                img {
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
    .page-01 {
        width: 100%;
        height: auto;
        background-color: #F7F4EB;
        position: relative;
        border-bottom: 0.006vw solid #181818;
        .page-01__navbar {
            padding-top: 1.5vw;
            padding-bottom: 1.5vw;
            border-bottom: 0.0006vw solid #181818;
            .container {
                display: flex;
                justify-content: space-between;
            }
            .navbar__contact {
                width: 14vw;
                height: 3vw;
                background-color: #181818;
                color: #FFFFFF;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
            }
        }
        .page-01__header-container {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            border-bottom: 0.0006vw solid #181818;
            padding-bottom: 1.5vw;
            .page-01__header {
                margin-top: 2vw;
                // margin-bottom: 0.7vw;
                display: block;
                width: 100%;
                text-align: center;
                line-height: 4.5vw;
            }
            .page-01__text {
                align-self: flex-start;
                margin-left: 20vw;
            }
        }
        .page-01__main-content {
            margin-top: 2vw;
            .container {
                display: flex;
                justify-content: space-between;
            }
            .main-content__left-side {
                
                display: flex;
                flex-direction: column;
                margin-top: 3vw;
                margin-bottom: 0.25vw;
                .mobile-button {
                    display: none;
                    width: 100%;
                    font-size: 5.7vw;
                    justify-content: center;
                    align-items: center;
                    color: #181818;
                    background-color: #F7F4EB;
                    border: 0.006vw solid #181818;
                }
                .left-side__border-group {
                    border-left: 0.0006vw solid #181818;
                    display: flex;
                    flex-direction: column;
                    padding-left: 1vw;
                    margin-bottom: 3vw;
                    .left-side__button {
                        cursor: pointer;
                    }
                    .about-marketer {
                        margin-top: -2vw;
                        line-height: 1.5vw;
                        .circle {
                            font-size: 9vw;
                            letter-spacing: 0vw;
                            margin-left: -0.5vw;
                        }
                    }

                    .left-side__text {
                        margin-bottom: 3vw;
                    }
                    .left-side__button {
                        margin-left: 15vw;
                        width: 19vw;
                        height: 3.5vw;
                        background-color: #181818;
                        color: #FFFFFF;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-bottom: 2vw;
                    }
                    img {
                        width: 6.7vw;
                        height: 6.7vw;
                        margin-bottom: 0.5vw;
                    }
                }

            }
            .main-content__right-side {
                height: 100%;
                width: 47vw;
                position: relative;
                margin-left: 20vw;
                img {
                    width: 100%;
                    height: auto;
                }
            }

        }
    }
    .page-02 {
        width: 100%;
        background-color: #181818;
        display: flex;
        color: #FFFFFF;
        .container {
            display: flex;
            
        }
        .page-02__left-content {
            margin-top: 7.1vw;
            margin-right: 1.5vw;
            position: relative;
            .border-click {
                background-size: cover;
                position: absolute;
                top: 0;
                left: 54%;
                width: 11vw;
                height: 5.2vw;
            }
        }
        .page-02__right-content {
            margin-top: 7.1vw;
            .right-content__text {
                display: flex;
                width: 60vw;
                span {
                    width: 30vw;
                }
                
            }
            .right-content__links {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-row-gap: 1.4vw;
                grid-column-gap: 1.4vw;
                margin-top: 2.5vw;
                margin-bottom: 8.8vw;
                a {
                    width: 14vw;
                    height: 3.8vw;
                    border: 0.0006vw solid #FFFFFF;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
    .page-03 {
        width: 100%;
        background-color: #F7F4EB;
        .page-03__header {
            padding-top: 5vw;
            display: block;
            margin-bottom: 1.6vw;
        }
        .page-03__content {
            border-top: 0.0006vw solid #181818;
            display: flex;
            padding-bottom: 5vw;
            .content__quiz-information {
                width: 23vw;
                padding-left: 0.5vw;
                padding-right: 0.5vw;
                margin-top: 4.4vw;
                border-left: 0.0006vw solid #181818;
                .about-marketer {
                    line-height: 1.5vw;
                    .circle {
                        font-size: 9vw;
                        letter-spacing: 0vw;
                        margin-left: -0.5vw;
                        display: inline;
                    }
                }
                span {
                    display: block;
                }
                .medium-regular {
                    margin-bottom: 5vw;
                }
            }
            .content__quiz {
                width: 62vw;
                border-left: 0.0006vw solid #181818;
                display: flex;
                align-items: flex-start;
                flex-direction: column;
                justify-content: center;
                padding-left: 1.4vw;
                .question {
                    // display: flex;
                    // justify-content: center;
                    // align-items: center;
                    // flex-direction: column;
                    .next-question {
                        width: 14vw;
                        height: 3.8vw;
                        background-color: #181818;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-top: 1.4vw;
                        color: #FFFFFF;
                        cursor: pointer;
                    }
                    .answer-wrapper {
                        display: flex;
                        flex-wrap: wrap;
                        label {
                            position: relative;
                            width: 30vw;
                            input {
                                background-color: transparent;
                                width: 100%;
                                height: 3.8vw;
                                border: 0.0006vw solid #181818;
                                margin-top: 1.4vw;
                                text-align: center;

                            }
                        }

                        .answer-question {
                            width: 14vw;
                            height: 3.8vw;
                            border: 0.0006vw solid #181818;
                            margin-right: 1.4vw;
                            margin-top: 1.4vw;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            cursor: pointer;
                            text-align: center;
                            user-select: none;
                            input {
                                position: absolute;
                                opacity: 0;
                                cursor: pointer;
                                height: 0;
                                width: 0;
                            }
                            .checkmark {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                position: absolute;
                                top: 0;
                                left: 0;
                                height: 100%;
                                width: 100%;
                                background-color: transparent;
                            }
                            .checkmark:after {
                                content: "";
                                position: absolute;
                                display: none;
                            }
                        }
                        .answer-question:hover input ~ .checkmark {
                            background-color: transparent;
                        }
                        .answer-question input:checked ~ .checkmark {
                            background-color: #181818;
                            color: #F7F4EB;
                        }

                          .answer-question input:checked ~ .checkmark:after {
                            display: block;
                          }
  
                    }
                    .question-title {

                    }
                }
            }
        }
    }
    .page-04 {
        width: 100%;
        padding-top: 5.5vw;
        background-color: #181818;
        color: #FFFFFF;
        position: relative;
        .page-04__content {
            display: flex;
            .content__left-side {
                width: 54vw;
                padding-bottom: 11vw;
                .content__text-group {
                    display: flex;
                    align-items: center;
                }
                .content__text {
                    font-weight: 700;
                    margin-top: 0.8vw;
                    display: block;
                    margin-bottom: 3.75vw;
                    margin-left: 7.6vw;
                }
                .content__decorative-number {
                    font-weight: 700;
                    font-size: 2.5vw;
                    margin-right: 3.8vw;
                }
                .content__list {
                    span {
                        display: block;
                        width: 25vw;
                    }
                    .content__text-marketer {
                        font-weight: 700;
                        margin-top: 1vw;
                        margin-bottom: 1vw;
                        margin-left: 8vw;
                        .circle {
                            font-size: 7vw;
                            letter-spacing: 0vw;
                            margin-left: -0.5vw;
                            display: inline;
                        }
                    }
                    .content__text-marketer-about {
                        margin-left: 9.7vw;
                    }
                    .content__text-developer {
                        font-weight: 700;
                        margin-top: 2vw;
                        margin-bottom: 1vw;
                        margin-left: 14.5vw;
                        .circle {
                            font-size: 7vw;
                            letter-spacing: 0vw;
                            margin-left: -0.5vw;
                            display: inline;
                        }
                    }
                    .content__text-developer-about {
                        margin-left: 16.2vw;
                    }
                    .content__text-designer {
                        font-weight: 700;
                        margin-top: 2vw;
                        margin-bottom: 1vw;
                        margin-left: 21vw;
                        .circle {
                            font-size: 7vw;
                            letter-spacing: 0vw;
                            margin-left: -0.5vw;
                            display: inline;
                        }
                    }
                    .content__text-designer-about {
                        margin-left: 22.7vw;
                    }
                }
            }
            .content__right-side {
                color: #181818;
                width: 41.4vw;
                background-color: #F7F4EB;
                position: absolute;
                right: 0;
                padding-bottom: 5.2vw;
                span {
                    display: block;
                    margin-left: 7.6vw;
                }
                .big-bold {
                    margin-top: 5vw;
                    margin-bottom: 0.8vw;

                }
            }
        }

    }
    .page-05 {
        width: 100%;
        background-color: #F7F4EB;
        position: relative;
        .page-05__header {
            padding-top: 4.2vw;
            margin-bottom: 3.8vw;
            display: block;
        }
        .question-img {
            font-size: 70vw;
            line-height: 45vw;
            letter-spacing: 3vw;
            position: absolute;
            font-weight: 700;
            top: 0%;
            right: 0%;
            color: #181818;
        }
        .page-05__form {
           form {
               font-size: 1.4vw;
               padding-bottom: 5.6vw;
               .dp-block {
                   display: block;
                   input {
                       margin-top: 0.4vw;
                       text-align: center;
                   }
               }
               .rectangle {
                   width: 22vw;
                   height: 2.7vw;
                   margin-bottom: 1.7vw;
                   background-color: transparent;
                   border: 0.0006vw solid #181818;
               }
               .radio-btns-container {
                    width: 22vw;
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 2.5vw;
                    margin-top: 0.4vw;
                   .radio-btn {
                    display: block;
                    position: relative;
                    width: 6.4vw;
                    height: 2.7vw;
                    cursor: pointer;
                    font-size: 0.9vw;
                    user-select: none;
                    
                  }
                  
                  /* Hide the browser's default checkbox */
                  .radio-btn input {
                    position: absolute;
                    opacity: 0;
                    cursor: pointer;
                    height: 0;
                    width: 0;
                  }
                  
                  /* Create a custom checkbox */
                  .checkmark {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                    background-color: transparent;
                    border: 0.0006vw solid #181818;
                  }
                  .radio-btn:hover input ~ .checkmark {
                    background-color: transparent;
                  }
                  
                  /* When the checkbox is checked, add a blue background */
                  .radio-btn input:checked ~ .checkmark {
                    background-color: #181818;
                    color: #F7F4EB;
                  }
                  
                  /* Create the checkmark/indicator (hidden when not checked) */
                  .checkmark:after {
                    content: "";
                    position: absolute;
                    display: none;
                  }
                  
                  /* Show the checkmark when checked */
                  .radio-btn input:checked ~ .checkmark:after {
                    display: block;
                  }
               }
               .submit-btn {
                   
                   display: block;
                   cursor: pointer;
               }
              
              /* Style the checkmark/indicator */
           }
        }
        .page-05__footer {
            height: 5.5vw;
            background-color: #181818;
            .container {
                display: flex;
                align-items: center;
                span {
                    margin-right: 22.2vw;
                    color: #F7F4EB;
                }
            }
        }
    }
    .page-06 {
        background-color: #FFFFFF;
        height: 10vw;
        width: 100%;
    }
}

